body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: gray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#nav{
    min-height:-webkit-max-content;
    min-height:max-content;
    display: flex;
    justify-content: center;
    background-color: black;
    font-family:"Times New Roman", Times, serif;
    font-size:larger;
    font-weight: bolder;
}



.bar{
    border: 1px solid white;
    border-radius:10%;
    background-color:black;
    margin: 20px;
    width:40%;
    height:10rem;
}

#war{
    width:220vh;
    border-radius:7px;
    display:inline-flex;
    padding:20px;
    background-color:black;
    overflow-x: scroll;
}

.fill{
    background-color: aliceblue;
    width:auto;
    padding: 0.5px;
}
.hs{
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    padding: 10px;
    margin: 10px;
}
.box{
    background-color: rgba(45, 44, 44, 0.9);
    min-height:150px;
    border-radius:10px;
    color: aliceblue;
    padding:10px;
    font-weight: bolder;
    margin:10px;
    width:auto;
    display:flex;
    overflow-x:auto;
    
}
input{
    border: 1px solid white;
    text-align: center;
    padding:3px;
    border-radius:5px;
    margin:15px;
    font-size: larger;
    min-width:30%;
    margin-left:25px;
    height: 25px;
    color-scheme: dark;
    background-color: black;
}
#go{
    margin:20px;
    padding:30px;
    padding-top:10px;
    margin-left:30%;
    min-width:150px;
    
}
select{
    color-scheme: dark;
    background-color: black;
    border-radius:10px;
    margin: 10px;
    text-align: center;
    font-size: larger;
    font-weight: bold;
    padding: 15px;
    padding-top:5px;
    padding-bottom:5px;
    float: right;
    min-width:100px;
}
#mon{
    min-width:70%;
    text-align: center;
}
#em{
    padding-top:5%;
    font-family:'fantasy';
    display:flex;
    justify-content:  center;
    padding-bottom:5%;
    font-size: larger;
}
#blo{
    border-style:dashed;
    border-color: aliceblue;
    padding: 100px;
    text-align: center;
}
.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: black;
    width: auto;
    
    
  }
  
  .expense-item__description {
    display:flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1 1;
    align-items: center;
    
  }
  
  .expense-item h1 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1 1;
    margin: 0 1rem;
    color: white;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color:rgb(32, 31, 31);
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
  }
  
  @media (min-width: 580px) {
    .expense-item__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1 1;
    }
  
    .expense-item__description h1 {
      font-size: 1.25rem;
    }
  
    .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }
  .dateex{
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
    align-items: center;
  }
  .contain{
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin:10px;
    border-radius: 12px;
    background-color:rgba(46, 45, 45, 0.9);
    justify-content: space-between;
    width: auto;
  }
  button{
    cursor: pointer;
    padding-left:5px;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: 350;
    margin-left: 10px;
    color:white;
    background-color:black;
    border:1px solid #fff;
    
  }
