.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin: 1rem 0;
    border-radius: 12px;
    background-color: black;
    width: auto;
    
    
  }
  
  .expense-item__description {
    display:flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
    align-items: center;
    
  }
  
  .expense-item h1 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color:rgb(32, 31, 31);
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
  }
  
  @media (min-width: 580px) {
    .expense-item__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1;
    }
  
    .expense-item__description h1 {
      font-size: 1.25rem;
    }
  
    .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }
  .dateex{
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
    align-items: center;
  }
  .contain{
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    padding: 0.5rem;
    margin:10px;
    border-radius: 12px;
    background-color:rgba(46, 45, 45, 0.9);
    justify-content: space-between;
    width: auto;
  }
  button{
    cursor: pointer;
    padding-left:5px;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: 350;
    margin-left: 10px;
    color:white;
    background-color:black;
    border:1px solid #fff;
    
  }